import React from 'react'
import {Card, Steps, Button, Form,Upload,Table,Collapse,Switch,Slider,Modal} from 'antd'
import {inject,observer} from 'mobx-react'
import axios from 'axios'
import './css/formDeni2.css'

const Panel = Collapse.Panel


//
const marks = {
  0: "0",
  100: {
      style: {
          color: "#f50"
      },
      label: <strong>100%</strong>
  }
};

//
const columnsmutInfo = [
  {
    title: 'ID',
    dataIndex: 'id',
  },{
    title: 'ClassName',
    dataIndex: 'ClassName',
    width: '30%',
  }, {
    title: 'MethodName',
    dataIndex: 'MethodName',
  }, {
    title: 'RowNumber',
    dataIndex: 'RowNumber',
  }, {
    title: 'Operator',
    dataIndex: 'Operator',
  }
]

@inject('RegressionStore') @Form.create() @observer
class StepRegMSR extends React.Component{
  state = {
    loading:false,
    submitloading:false,
    pagination: {
      pageSize: 10
    },
    datamutinfo:[],
    isreduced:false,
    range:[40,60]
  }
  // id,
  componentDidMount() {
    this.getRemoteData()
  }

  getRemoteData(params) {
    this.setState({
      loading: true
    });
    axios.get('http://localhost:5000/api/mutant/show/all', {
      params: {
        results: 10,
        // size: 200,
        projectId:'exp4j',
        ...params
      }
    }).then(res => {
      const pagination = {...this.state.pagination};
      pagination.total = this.props.RegressionStore.numofmutant;
      this.setState({
        loading: false,
        datamutinfo: res.data.results,
        pagination
      });
    }).catch((error) =>{
      Modal.error({
        title: 'This is an error message',
        content: error.response,
      });
    })
  }

  handleTableChange = (pagination) => {
    const pager = {...this.state.pagination};
    pager.current = pagination.current;
    this.setState({
      pagination: pager,
    });
    this.getRemoteData({
      results: pagination.pageSize,
      page: pagination.current,
    })
  }
// id
  handleSubmit = ()=>{
    let isMock=true;
    this.setState({
      submitloading:true
    });
    if(isMock){
      setTimeout(()=>{
        this.setState({
          submitloading:false
        })
        this.props.RegressionStore.setNumofmutantre(186)
        this.props.RegressionStore.setNumofts(65)
        this.props.RegressionStore.setMscore(0.9038)
        this.props.RegressionStore.setCurrent(2)
      },2000);
    }else{
      axios.get('http://localhost:5000/api/mutant/reduce', {
        params: {
          projectId: 'exp4j',
          hasModel: this.props.RegressionStore.hasmodel,
          rangeS:this.state.range[0],
          rangeE:this.state.range[1]
        }
      }).then(res => {
        this.setState({
          submitloading:false
        })
        this.props.RegressionStore.setNumofmutantre(res.data.mutantSize);
        this.props.RegressionStore.setNumofts(res.data.testSize);
        this.props.RegressionStore.setMscore(res.data.mscore);
        this.props.RegressionStore.setCurrent(2);
      }).catch((error) =>{
        Modal.error({
          title: 'This is an error message',
          content: error.response,
        });
      })
    }
  }

  onChange = value => {
    console.log("range=>", value);
    this.setState({range: value});

  };

  render(){
    return (
      <div>
        <Card bordered={false} title='' style={{marginBottom: 10}} id='remoteLoading'>
          <Collapse defaultActiveKey={['1']}>
                <Panel header={`${this.props.RegressionStore.numofmutant}`} key="1">
                    <Table 
                    rowKey={record => record.id}
                    loading={this.state.loading}
                    dataSource={this.state.datamutinfo}
                    pagination={this.state.pagination}
                    onChange={this.handleTableChange}
                    columns={columnsmutInfo} style={styles.tableStyle}/>
                </Panel>
          </Collapse>  
        </Card>
        <Card bordered={false} title='' id='remoteLoading'>
        <Switch onChange={(checked) => this.setState({isreduced: checked})}/>
        {this.state.isreduced?
          <Slider
          marks={marks}
          range
          onChange={this.onChange}
          defaultValue={[20,50]}
          />
        :''}
        <div id="step3">
            <Button type="primary" onClick={this.handleSubmit} loading={this.state.submitloading}></Button>
            <Button onClick={()=>this.props.RegressionStore.setCurrent(0)} style={{ marginLeft: 8 }}></Button>
        </div>
        </Card>
        
      </div>
    )
  }
}

const styles = {
  steps:{
    maxWidth:750,
    margin: '16px auto'
  }
}

export default StepRegMSR;


