import React from 'react'
import {Card, Divider, Button, Form,Icon,BackTop,Upload,message,Collapse,Radio,Modal} from 'antd'
import {inject,observer} from 'mobx-react'
import './css/formDeni2.css'
import {withRouter} from 'react-router-dom'
import axios from 'axios'
import { isAuthenticated } from '../../utils/Session'


const Dragger = Upload.Dragger;
let projectid='';

  
// 
// 
const propsfileupload = {
  name: 'file',
  multiple: false,
  // action: '//jsonplaceholder.typicode.com/posts/',
  action:'http://localhost:5000/api/upload',
  onChange(info) {
    const status = info.file.status;
    if (status !== 'uploading') {
      console.log(info.file, info.fileList);
    }
    if (status === 'done') {
      projectid=info.file.name;
      projectid=projectid.split('.')[0];
      if(projectid.indexOf('Project')!=-1){
        projectid=projectid.substring(0,projectid.length-7)
      }
      message.success(`${info.file.name} file uploaded successfully.`);
    } else if (status === 'error') {
      message.error(`${info.file.name} file upload failed.Please upload zip file.`);
    }
  },
}


@inject('RegressionStore') @Form.create() @observer @withRouter
class StepRegUpload extends React.Component{
  state={
    loading:false,
    submitloading:false,
    models:[]
  }


  // 
  componentDidMount() {
    this.getModelData()
  }

  showModalType = (type) => {
    switch (type) {
      case 'error':
        Modal.error({
          title: 'This is an error message',
          content: '',
          onOk:()=>{this.props.history.push('/reduction/new')}
        })
        break;
      default:
        Modal.warning({
          title: 'This is a warning message',
          content: '',
        })
        break;
    }
  }
  

  getModelData(params) {
    let isMock=false;
    this.setState({
      loading: true
    })
    if(isMock){
      const models=['exp4j','commos-lang'];
      if(models.length==0){
        this.showModalType('error')
      }else{
        setTimeout(()=>{
          this.setState({
              loading:false,
              models:models
          })   
        },2000)  
      }
    }else{
      axios.get('http://localhost:5000/api/model/query', {
        params: {
          username: isAuthenticated()
        }
      }).then(res => {
          let models=res.data.models;
          if(models.length==0){
            this.showModalType('error');
          }else{
            this.setState({
              loading:false,
              models:models
            }) 
          }
      })
    }  
  }
  // id
  nextStep = () =>{
    let isMock=true;
    if(projectid==''){
      this.showModalType('warning');
    }else{
      this.setState({
        submitloading:true
      });
      if(isMock){
        setTimeout(()=>{
         this.setState({
           submitloading:false
          });
          this.props.RegressionStore.setNumofmutant(411);
          this.props.RegressionStore.setCurrent(1);
          this.props.RegressionStore.setprojectid(projectid);
          this.props.RegressionStore.sethasmodel(true);
          console.log(projectid);
       },2000);
      }else{
        axios.get('http://localhost:5000/api/mutant/generate', {
          params: {
            projectId: projectid
          }
        }).then(res => {
          if(res.data.error!=''){
            Modal.error({
              title: 'This is an error message',
              content: res.data.error,
            })
          }else{
            this.setState({
              submitloading: false
            });
            this.props.RegressionStore.setNumofmutant(res.data.length);
            this.props.RegressionStore.setCurrent(1);
            this.props.RegressionStore.setprojectid(projectid);
            this.props.RegressionStore.sethasmodel(true);
          }
          
        }).catch((error) =>{
          Modal.error({
            title: 'This is an error message',
            content: error.response,
          });
        })
      }
      
    }    
  }

  onChange = e => {
    console.log('radio checked', e.target.value);
  };

  render(){
    return (
      <div>
        <Card bordered={false} className='card-item' title='' loading={this.state.loading}>
            <span style={{marginRight:10}}></span><Radio.Group onChange={this.onChange} value={this.state.models[0]} style={{marginBottom:25}} >
                {this.state.models.map((value,index)=>{
                    return(<Radio key={value} value={value}>{value}</Radio>)
                })}
            </Radio.Group>
              <Dragger {...propsfileupload}>
                <p className="ant-upload-drag-icon">
                  <Icon type="inbox"/>
                </p>
                <p className="ant-upload-text">zip</p>
              </Dragger>
        </Card>
        <div id="step3"><Button type='primary' onClick={this.nextStep} loading={this.state.submitloading}></Button></div>     
        <Divider/>
        <div className='desc'>
          <h3></h3>
          <h4></h4>
          <p>
            pitest
          </p>
          <p>
            
          </p>
        </div>
        <BackTop visibilityHeight={200} style={{right: 50}}/>
      </div>
    )
  }
}

const styles = {
    desc:{
      padding:'0 56px',
    }
  }

export default StepRegUpload;


