import React from 'react'
import {Card, Steps, Button, Upload,message,Table,Collapse,Tabs,Modal} from 'antd'
import {inject,observer} from 'mobx-react'
import axios from 'axios'
import './css/formDeni2.css'



//
const columnscoveredInfo = [
  {
    title: 'id',
    dataIndex: 'id',
  }, {
    title: 'PackageName',
    dataIndex: 'PackageName',
    width: '20%',
  }, {
    title: 'ClassNumber',
    dataIndex: 'ClassNumber',
  }, {
    title: 'LineNumber',
    dataIndex: 'LineNumber',
  }, {
    title: 'LineCoverage',
    dataIndex: 'LineCoverage',
  }, {
    title: 'BranchCoverage',
    dataIndex: 'BranchCoverage',
  }, {
    title: 'complexity',
    dataIndex: 'complexity',
  }
]

  //
const columnsmuttestInfo = [
  {
    title: '',
    dataIndex: 'numofmutantre',
  }, {
    title: '',
    dataIndex: 'numofts',
  }, {
    title: '',
    dataIndex: 'mscore',
  }
]

@inject('RegressionStore') @observer
class StepRegTRG extends React.Component{
  state = {
    loading:false,
    submitloading:false,
    pagination: {
      pageSize: 8
    },
    datacoveredinfo:[],
    datamuttestinfo:[]
  }
// id
  getRemoteCoveredData(params) {
    this.setState({
      loading: true
    })
    axios.get('http://localhost:5000/api/test/coverage', {
      params: {
        projectId:'exp4j',
        results: 10,
        // size: 200,
        ...params
      }
    }).then(res => {
      const pagination = {...this.state.pagination};
      pagination.total = res.data.length;
      this.setState({
        loading: false,
        datacoveredinfo: res.data.results,
        pagination
      })
    }).catch((error) =>{
      Modal.error({
        title: 'This is an error message',
        content: error.response,
      });
    })
  }
// 
  getRemoteMuttestData(params) {
    this.setState({
      loading: true
    })
    const mockdata = [
      {
        key: '1',
        numofmutantre:this.props.RegressionStore.numofmutantre,
        numofts:this.props.RegressionStore.numofts,
        mscore:this.props.RegressionStore.mscore
      }
    ]
    setTimeout(()=>{
      this.setState({
        loading:false,
        datamuttestinfo:mockdata
      })
    },2000)
  }

  handleTableChange = (pagination) => {
    const pager = {...this.state.pagination};
    pager.current = pagination.current;
    this.setState({
      pagination: pager,
    });
    this.getRemoteCoveredData({
      results: pagination.pageSize,
      page: pagination.current,
    })
  }

  handleSubmit1 = ()=>{
    this.getRemoteCoveredData()
  }

  handleSubmit2 = ()=>{
    this.getRemoteMuttestData()
  }

  render(){
    return (
      <Card bordered={false} className='card-item' title=''>
        <Tabs defaultActiveKey="1">
          <Tabs.TabPane tab='' key='1'>
          <Button type="primary" style={{marginRight:20}} onClick={this.handleSubmit1} loading={this.state.loading}></Button>
          <Card bordered={false} title='' style={{marginBottom: 10}} id='remoteLoading'>
            <Table rowKey={record => record.id}
            loading={this.state.loading}
            dataSource={this.state.datacoveredinfo}
            pagination={this.state.pagination}
            onChange={this.handleTableChange}
            columns={columnscoveredInfo} style={styles.tableStyle}/>     
          </Card>
          </Tabs.TabPane>
          <Tabs.TabPane tab='' key='2'>
          <Button type="primary" style={{marginRight:20}} onClick={this.handleSubmit2} loading={this.state.loading}></Button>
          <Card bordered={false} title='' style={{marginBottom: 10}} id='basicUsage'>
          <Table loading={this.state.loading} dataSource={this.state.datamuttestinfo} columns={columnsmuttestInfo} style={styles.tableStyle}/>
        </Card>
          </Tabs.TabPane>
        </Tabs>
      </Card>
    );
  }

}

const styles = {
  steps:{
    maxWidth:750,
    margin: '16px auto'
  }
}

export default StepRegTRG;




