import React from 'react'
import {Card, Steps} from 'antd'
import {inject,observer} from 'mobx-react'
import CustomBreadcrumb from '../../components/CustomBreadcrumb/index'
import './css/formDeni2.css'
import TypingCard from '../../components/TypingCard'

import StepRegUpload from './StepRegUpload'
import StepRegMSR from './StepRegMSR'
import StepRegShowRes from './StepRegShowRes'
import StepRegTRG from './StepRegTRG'


const { Step } = Steps;


@inject('RegressionStore') @observer
class RegressionScene extends React.Component {
  showStep = ()=>{
    switch (this.props.RegressionStore.current){
      case 1 : return <StepRegMSR/>
      case 2: return <StepRegShowRes/>
      case 3: return <StepRegTRG/>
      default: return <StepRegUpload/>
    }
  }
  render () {
    return (
      <div>
        <CustomBreadcrumb arr={['', '']}/>
        <TypingCard source=''/>
        <Card title='' bordered={false} style={{minHeight: 600}}>
          <Steps style={styles.steps} current={this.props.RegressionStore.current}>
            <Step title="" />
            <Step title="" />
            <Step title="" />
            <Step title=""></Step>
          </Steps>
          <div>{this.showStep()}</div>
        </Card>
      </div>
    )
  }
}

const styles = {
  steps:{
    maxWidth:750,
    margin: '16px auto'
  }
}

export default RegressionScene


