import React from 'react'
import {Card, Steps, Button,Icon,Table,Collapse,notification,Modal} from 'antd'
import {inject,observer} from 'mobx-react'
import axios from 'axios'
import './css/formDeni2.css'
import { isAuthenticated } from '../../utils/Session'

const Panel = Collapse.Panel

//
const columnsmutInfo = [
  {
    title: 'ID',
    dataIndex: 'id',
  },{
    title: 'ClassName',
    dataIndex: 'ClassName',
    width: '30%',
  }, {
    title: 'MethodName',
    dataIndex: 'MethodName',
  }, {
    title: 'RowNumber',
    dataIndex: 'RowNumber',
  }, {
    title: 'Operator',
    dataIndex: 'Operator',
  }
]

  @inject('RegressionStore') @observer
  class StepRegShowRes extends React.Component{
    state = {
      loading:false,
      submitloading:false,
      pagination: {
        pageSize: 8
      },
      datamutinfo:[],
    }
    componentDidMount() {
      this.getRemoteData()
    }
  //id 
  getRemoteData(params) {
    this.setState({
      loading: true
    })
    axios.get('http://localhost:5000/api/mutant/show/part', {
      params: {
        results: 10,
        projectId:'exp4j',
        ...params
      }
    }).then(res => {
      const pagination = {...this.state.pagination};
      pagination.total = this.props.RegressionStore.numofmutantre;
      this.setState({
        loading: false,
        datamutinfo: res.data.results,
        pagination
      })
    }).catch((error) =>{
      Modal.error({
        title: 'This is an error message',
        content: error.response,
      });
    })
  }
  
    handleTableChange = (pagination) => {
      const pager = {...this.state.pagination};
      pager.current = pagination.current;
      this.setState({
        pagination: pager,
      });
      this.getRemoteData({
        results: pagination.pageSize,
        page: pagination.current,
      })
    }
  
    handleSubmit = ()=>{
      this.props.form.validateFields((err,values)=>{
        if (!err){
          this.setState({
            loading:true
          })
         setTimeout(()=>{
           this.setState({
             loading:false
           })
           this.props.RegressionStore.setCurrent(3)
         },2000)
        }
      })
    }

    handelDownloadRes=()=>{
      axios.get('http://localhost:5000/api/result/download', {
        params: {
          projectId:'exp4j'
        }
      }).then(res  => {
        // 
        let eleLink = document.createElement('a');
        eleLink.download = "results.csv";
        eleLink.style.display = 'none';
        // blob
        // console.log(res.data);
        let blob = new Blob([res.data]);
        eleLink.href = URL.createObjectURL(blob);
        // 
        document.body.appendChild(eleLink);
        eleLink.click();
        // 
        document.body.removeChild(eleLink);
      }).catch((error) =>{
        Modal.error({
          title: 'This is an error message',
          content: error.response,
        });
      })
    }
  
  
    render(){
      return (
          <div>
            <div className='icon-box'>
              <Icon type='check-circle'/>
            </div>
            <div>
              <h3 className='success'></h3>
            </div>
            <Card bordered={false} title='' style={{marginBottom: 10}} id='remoteLoading'>
              <Collapse defaultActiveKey={['1']}>
                    <Panel header={`${this.props.RegressionStore.numofmutantre}`} key="1">
                        <Table rowKey={record => record.id}
                        loading={this.state.loading}
                        dataSource={this.state.datamutinfo}
                        pagination={this.state.pagination}
                        onChange={this.handleTableChange}
                        columns={columnsmutInfo} style={styles.tableStyle}/>
                    </Panel>
              </Collapse>  
            </Card>
            <div id='step3'>
              <Button type='primary' onClick={()=>this.props.RegressionStore.setCurrent(3)}></Button>
              {/* id */}
              <Button style={{ marginLeft: 8 }} onClick={this.handelDownloadRes}></Button>
              <Button onClick={()=>this.props.RegressionStore.setCurrent(0)} style={{ marginLeft: 8 }}></Button>
            </div>
          </div>
      )
    }
  }
  const styles = {
    steps:{
      maxWidth:750,
      margin: '16px auto'
    }
  }

export default StepRegShowRes;


